@import url(https://fonts.googleapis.com/css2?family=Belleza&display=swap);
/* Meyers CSS Reset */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* My styles */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #000;
}

body::-webkit-scrollbar-thumb {
  background: #fff;
}

body {
  background: #000;
  color: white;
  font-family: 'Belleza', sans-serif;
  letter-spacing: 0.5rem;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: currentColor;
}

ul,
li {
  list-style: none;
}

.App {
  position: relative;
}

.App.nav-open {
  position: fixed;
  width: 100vw;
}

.header {
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  text-transform: uppercase;
  height: 10vh;
  position: relative;
  z-index: 1;
  opacity: 0;
  animation: headerIntro 0.4s easeOut forwards;
}

@keyframes headerIntro {
  to {
    opacity: 1;
  }
}

.title {
  z-index: 11;
}

.burger {
  cursor: pointer;
  z-index: 11;
}

.line {
  width: 50px;
  height: 2px;
  background-color: white;
  margin: 5px 0 5px auto;
  transition: all 0.4s ease;
}
.line2 {
  width: 35px;
}
.line3 {
  width: 20px;
}
.line.nav-open {
  width: 30px;
}
.line1.nav-open {
  transform: rotate(-45deg) translate(-5px, 5px);
}
.line2.nav-open {
  transform: translateX(100%);
  opacity: 0;
}
.line3.nav-open {
  transform: rotate(45deg) translate(-5px, -5px);
}

.title {
  font-size: 1rem;
}

.nav-menu {
  width: 100%;
  height: 100vh;
  position: absolute;
  background-color: black;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-top: 10vh;
  padding: 4rem 0;
  z-index: 10;
  transform: translateX(-100%);
  transition: all 0.8s ease-out;
}

.nav-menu.nav-open {
  transform: translateX(0%);
}

.nav-item {
  opacity: 0;
  transform: translateX(-100px);
  animation: navLinkFadeOut 0.3s ease forwards;
}

.nav-item:nth-child(1).nav-open {
  animation: navLinkFadeIn 0.5s ease-out 0.5s forwards;
}
.nav-item:nth-child(2).nav-open {
  animation: navLinkFadeIn 0.5s ease-out 0.6s forwards;
}
.nav-item:nth-child(3).nav-open {
  animation: navLinkFadeIn 0.5s ease-out 0.7s forwards;
}
.nav-item:nth-child(4).nav-open {
  animation: navLinkFadeIn 0.5s ease-out 0.8s forwards;
}

@keyframes navLinkFadeIn {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes navLinkFadeOut {
  from {
    opacity: 1;
    transform: translateX(0px);
  }
  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}

@media only screen and (min-width: 550px) {
  .nav-menu,
  .title {
    font-size: 1.5rem;
  }
}

@media only screen and (min-width: 1024px) {
  .nav-menu,
  .title {
    font-size: 2rem;
  }
}

.photo-grid {
  width: 100vw;
  position: relative;

  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: 1fr;
  grid-auto-rows: 400px;

  padding: 2rem;
}

.card {
  overflow: hidden;
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 2.5px;
}

.card:nth-child(8) img {
  object-position: bottom;
}

footer {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 4rem;
}

.card-tall {
  grid-row: span 2 / auto;
}

@media only screen and (min-width: 800px) {
  .photo-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 350px;
  }

  .card-wide {
    grid-column: span 2 / auto;
  }

  .card:nth-child(15),
  .card:nth-child(20),
  .card:nth-child(18) {
    display: none;
  }
}

@media only screen and (min-width: 1200px) {
  .photo-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 400px;
  }

  .card:nth-child(15),
  .card:nth-child(20),
  .card:nth-child(18) {
    display: block;
  }
}

.about-main {
  padding: 1rem 0;
}

.hero {
  display: flex;
  min-height: 80vh;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.hero-desc,
.hero-img {
  width: 80vw;
  padding: 1rem 0;
}

.hero-desc p {
  color: white;
  letter-spacing: 0.2rem;
  font-size: 1rem;
}

.hero-img {
  height: 60vh;
}

.hero-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 2.5px;
}

@media only screen and (min-width: 800px) {
  .hero {
    flex-direction: row;
  }

  .hero-desc,
  .hero-img {
    width: 40vw;
  }

  .hero-desc p {
    font-size: 1.25rem;
  }
}
@media only screen and (min-width: 1200px) {
  .hero-desc p {
    font-size: 1.5rem;
  }
}

.contact-main {
  padding-bottom: 1rem;
}

form {
  width: 80vw;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  margin: auto;
}

label {
  color: white;
  letter-spacing: 0.25rem;
  padding-bottom: 1rem;
  padding-top: 2rem;
}

input,
textarea,
button {
  border: none;
  border-radius: 2.5px;
  padding: 1rem;
}

button {
  margin-top: 2rem;
  border: none;
  align-self: center;
  padding: 1rem 4rem;
  background-color: white;
  cursor: pointer;
  font-family: 'Belleza', sans-serif;
  letter-spacing: 0.25rem;
}

button:hover,
button:focus,
input:valid,
textarea:valid {
  background: #323232;
  color: white;
  transition: all 300ms ease;
  border: 1px solid white;
}

@media only screen and (min-width: 800px) {
  label,
  button {
    font-size: 1.5rem;
  }
}

@media only screen and (min-width: 1024px) {
  form {
    width: 60vw;
  }
}

@media only screen and (min-width: 1200px) {
  form {
    width: 50vw;
  }
}

.home {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
  text-transform: uppercase;

  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  border-radius: 2.5px;
}

.chevron {
  display: none;
}

.home-header {
  padding-top: 5rem;
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.home-header h1 {
  font-size: 7rem;
  text-shadow: 0px 0px 50px rgba(0, 0, 0, 0.3);
}

.home-header h2 {
  font-size: 2rem;
  text-shadow: 0px 0px 50px rgba(0, 0, 0, 0.3);
}

.home-main {
  height: 50vh;
  text-align: center;
  position: relative;
}

.home-main:hover .home-nav-item {
  opacity: 1;
}

.home-nav-menu {
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 50vh;
}

.home-nav-menu:hover .home-nav-item {
  opacity: 0.5;
}

.home-nav-menu .home-nav-item:hover {
  opacity: 1;
  transform: scale(1.1);
}

.home-nav-item {
  transition: all 0.3s ease;
}

@media only screen and (min-width: 550px) {
  .home-header h2 {
    font-size: 2.4rem;
  }

  .home-header h1 {
    font-size: 8rem;
  }

  .home-nav-menu {
    font-size: 2.4rem;
  }
}

@media only screen and (min-width: 1024px) {
  .home-header h1 {
    font-size: 10rem;
  }

  .home-header h2 {
    font-size: 3rem;
  }

  .home-nav-menu {
    display: none;
    font-size: 4rem;
  }

  .home-nav-menu .home-nav-item {
    transform: rotate(90deg);
    opacity: 0;
  }

  .home-nav-menu .home-nav-item:hover {
    transform: rotate(0deg);
  }

  .chevron {
    display: block;
    width: 3rem;
    transition: all 1s ease;
    position: absolute;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
  }

  .home-main {
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50vh;
    transition: all 1s ease;
  }

  .home-main:hover {
    height: 50vh;
    margin-top: 10vh;
    flex-direction: column;
  }

  .home-main:hover .home-nav-menu {
    display: flex;
    flex-direction: row;
  }

  .home-main:hover .chevron {
    transform: rotate(180deg) translateX(50%);
  }
}

